   @font-face {
    font-family: 'Cutive Mono Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Cutive Mono Regular'), url(/facerecognition/static/media/CutiveMono-Regular.958f3b37.woff) format('woff');
    }




body {
  margin: 0;
  padding:  0;
  font-family: Cutive Mono Regular, sans-serif;
  background: linear-gradient(to right, rgb(0, 179, 255), rgb(255, 0, 247));
  
}

button {
  cursor:  pointer;
}

.Tilt {
	background: linear-gradient(to right, rgb(0, 179, 255), rgb(255, 0, 247));
	
}
.form {
	width: 700px;
	background:
	linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
	linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
	linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
	linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
	linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
	linear-gradient(#1d1d1d 25%, #1a1a1a 25%, #1a1a1a 50%, transparent 50%, transparent 75%, #242424 75%, #242424);
	background-color: #131313;
	background-size: 20px 20px;
}
.App {
  text-align: center;
}

.center {
  display:  flex;
  justify-content: center;

}

.particles {
  position:  fixed;
  top:  0;
  right:  0;
  bottom:  0;
  left:  0;
  z-index: -3;
}
.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
